import fetch from 'isomorphic-unfetch'



Quote.getInitialProps = async function () {
    const res = await fetch("https://gist.githubusercontent.com/camperbot/5a022b72e96c4c9585c32bf6a75f62d9/raw/e3c6895ce42069f0ee7e991229064f167fe8ccdc/quotes.json");
    const data = await res.json();
    return {
        quote: data
    }
}

const Quote = (props) => {
    return (
        <div>
            {props.quote.map((quote, i) => (
                <div>
                    <div className="quote-text">
                        <span id="text">
                            "{quote.quote[i]}"
                        </span>
                    </div>
                    <div className="author">
                        - <span id="author">
                            {quote.author[i]}
                        </span>
                    </div>
                </div>
            ))}
        </div>
    )
}

export default Quote